@font-face {
  font-family: 'Playfair';
  font-style: normal;
  font-weight: 400;

  src: url('./assets/fonts/PlayfairDisplay-Regular.ttf') format('truetype')
}

@font-face {
  font-family: 'Nexa';
  font-style: normal;
  font-weight: 400;

  src: url('./assets/fonts/Nexa-Light.otf') format('OpenType')
}

@font-face {
  font-family: 'NexaBold';
  font-style: normal;
  font-weight: 400;

  src: url('./assets/fonts/Nexa-Bold.otf') format('OpenType')
}

body{
  margin: 0;
  font-family: Nexa !important;
  direction: ltr;
  background-color: #f4f7fb !important;
}
a{
  font-family: NexaBold;
}
p, a{
  font-size: 14px !important;
}

a:hover{
  text-decoration: none !important;
}

ul{
  padding: 0;
  list-style-type: none;
}

header{
  background-color: #fff;
}

header .top-nav{
  padding: 20px 0;
  border-bottom: 1px solid #f5f5f5;
}

.login-icon{
  width: 17px;
  margin-right: 5px;
}

.top-nav-left span{
  font-weight: bold;
}

.who{
  margin-left: 15px;
  color: #000;
}

.who:hover{
  color: #f00074;
}

.logo{
  text-align: center;
}

.logo img{
  width: 151px !important
}

.top-nav-left{
  padding: 17px 0 0px
}

.ask{
  padding-top: 15px;
}

.ask img{
  width: 160px;
}

.navbar-nav{
  margin: 0 auto;
}

.top-nav-right{
  text-align: right;
  padding: 17px 0 0px;
}

.top-nav-right ul{
  display: inline-flex;
  list-style-type: none;
  margin-bottom: 0;
}

.top-nav-right ul li{
  margin-right: 15px;
  font-weight: bold;
}
.top-nav-right ul li a{
  color: #565555;
  font-weight: bold;
  -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}

.top-nav-right ul li:hover a{
  color: #f00074;
}
.navbar-light .navbar-nav .nav-link{
    color: #535252 !important;
    background-color: transparent;
    border-radius: 25px;
    font-weight: bold;
    -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}
.navbar-light .navbar-nav .nav-link:hover{
  color: #f00074 !important;
  background-color: #f4f4f4;
}
.right-navbar{
  margin-bottom: 0;
}

.right-navbar li{
  padding: 10px 20px;
  position: relative;
  float: right;
  color: #535252;

}

.right-navbar li a{
  color: #535252;
  background-color: transparent;
  -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}

.right-navbar li:hover a{
  color: #f00074;
  
}


.right-navbar li::after{
  content: '';
  position: absolute;
  right: 0;
  top: 10px;
  height: 34px;
  width: 2px;
  background: #fafafa;
}

.search{
  padding-top: 15px !important;
}

.body-container{
  padding: 40px 0;
}

.login-card{

  text-align: center;
  background-color: #fff;
}

.login-card-img{
  background-image: url(./assets/imgs/Group_1805.png);
  background-position: center;
  background-size: cover;
  padding: 42px 0;
}

.social-login{
  padding: 30px;
  border-bottom: 1px solid #f4f4f4;
}
.col-padding{
  padding: 0px 5px !important;
}
.social-login-link{
  padding-top: 5px ;
  padding-bottom: 5px ;
  text-align: center;

}

.social-login-link img{
  height: 13px;
}

.google-login{
  background-color: #f4f7fb;
}

.facebook-login{
  background-color: #38549c;
}

.instagram-login{
  background: #d6249f;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
}

.login-form{
  padding: 30px ;
}

.subscribe{
  background-color: #fff;
  padding: 40px 0;
}
.subscribe h4{
  padding: 19px 0 0;
  font-family: Playfair;
  font-weight: bold;
}
.subscribe .go-button{
    float: right;
    height: 36px !important;
    color: rgba(255, 255, 255, 0.87) !important;
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
    background-color: #ffaec0 !important;
    margin-top: 14px;
    padding: 0px 43px;
}

.MuiFormControl-root {
  margin-bottom: 20px !important;
}

.login-form .submit{
  background-color: #f00074;
  color: #fff;
  margin: 10px 0;
}

.MuiInput-underline:before {
  left: 0;
  right: 0;
  bottom: 0;
  content: "\00a0";
  position: absolute;
  transition: border-bottom-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-bottom: 2px solid rgb(0, 0, 0) !important;
  pointer-events: none;
}

.forget-pass{
  float: right;
  color: #888787;
}
.sign-up-link a{
  color: #888787;
}
.MuiButton-root{
  border-radius: 25px !important;
}

.quill{
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
} 

.ql-toolbar.ql-snow{
  border-radius: 10px 10px 0px 0;
}

.ql-container.ql-snow{
  border-radius: 0px 0px 10px 10px;
}

.editor-h{
  text-align: center;
  font-weight: bold;
  margin: 10px 0 50px;
}

.ql-editor{
  min-height: 200px;
}

footer{
  background-color: #000;
  padding: 40px 0;
}

.top-footer{
  border-bottom: 1px solid #5f5f5f;
  padding-right: 10px;
  padding-left: 10px;
  padding-bottom: 23px;
}

.footer-logo{
  width: 150px;
  margin-bottom: 15px;
}

.footer-left-side{
  position: relative;
}

.footer-left-side ul{
  position: absolute;
  bottom: 20px;
  right: 10px;
}

.footer-tv{
  width: 30px !important;
  height: 30px !important;
  background-color: transparent !important;
  padding: 0px !important;
  margin-top: -4px !important;
}
.footer-tv img{
  width: 100%;
}

.social-media-list ul{
  display:flex;
  list-style-type: none;
  padding: 0;
}
.social-media-list ul li{
  background-color: #fff;
  margin-right: 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  padding: 4px;
  margin-bottom: 5px;
  -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}
.social-media-list ul li:hover{
  background-color: #f00074;
}

.social-media-list ul li:hover a{
  color: #fff ;
}

.footer-pref{
  max-width: 400px;
  font-size: 14px;
  color: #bfbfbf;
}

.bottom-footer{
  padding: 20px 15px 0px;
}

.footer-nav{
  display: flex;
  list-style-type: none;
  padding: 0px !important;
}

.footer-nav li{
  margin-right: 20px;
}

.footer-nav li:hover a{
  color: #f00074;
  -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}

.footer-nav li a{
  color: #fff;
  font-family: Nexa;
}

.bottom-footer-left{
  text-align: right;
  color: #fff;
}

.footer-left-side a{
  color: #000;
  -webkit-transition: .3s all ease-out;
  -moz-transition: .3s all ease-out;
  -o-transition: .3s all ease-out;
  transition:.3s all ease-in-out;
}

@media only screen and (max-width: 768px) {
  header .top-nav{
    padding: 10px 0 20px;
  }
  .top-nav-left{
    text-align: center;
  }
  .top-nav-right {
    text-align: center;
}
.who {
  margin-left: 15px;
  color: #000;
  float: right;
}
.top-nav-left span {
  font-weight: bold;
  float: left;
}
.subscribe{
  text-align: left;
}
.subscribe .go-button{
  float: unset;
}
.subscribe .row{
  margin: 0 !important;
}
  .navbar-collapse{
    text-align: center;
  }
  .top-nav-right ul li{
    margin-right: 10px;
  }
  .social-media-list ul{
    column-gap: 20px;
    flex-flow: wrap row;
  }
  .footer-left-side ul{
    position: unset;
  }
  .footer-nav{
    column-gap: 20px;
    flex-flow: wrap row;
  }
}
@media only screen and (max-width: 414px) {
  .subscribe{
    text-align: center;
  }
}